<template>
  <div class="page" v-show="load">
    <div class="page__content">
      <div class="page__header">
        <breadcrumbs />
        <h1 class="page__title">
          {{ place.title }}
        </h1>
      </div>

      <div class="place__body">
        <div class="place__image" v-if="place.title_picture.url">
          <img :src="place.title_picture.url" alt="" />
        </div>

        <div class="place__content">
          <div class="place__col-left">
            <div class="place__specs">
              <div class="place__specs-item" v-show="place.area">
                <div class="place__specs-title">Площадь</div>
                <div class="place__specs-value">{{ place.area }} кв.м.</div>
              </div>
              <div class="place__specs-item" v-show="place.capacity">
                <div class="place__specs-title">Вместимость</div>
                <div class="place__specs-value">
                  {{ place.capacity }}
                  {{
                    place.capacity
                      ? getNoun(
                          place.capacity,
                          "человек",
                          "человека",
                          "человек"
                        )
                      : ""
                  }}
                </div>
              </div>
              <div class="place__specs-item" v-show="place.type">
                <div class="place__specs-title">Тип площадки</div>
                <div class="place__specs-value">{{ place.type }}</div>
              </div>
              <div class="place__specs-item" v-show="place.work_time">
                <div class="place__specs-title">Время работы</div>
                <div class="place__specs-value">{{ place.work_time }}</div>
              </div>
            </div>
            <div class="place__advantage">
              <h3 class="place__advantage-title">Преимущество</h3>
              <div class="place__advantage-text">
                {{ place.advantages }}
              </div>
            </div>
            <div class="place__description">
              <div class="place__description-item">
                <h3 class="place__description-title">Оснащение</h3>
                <div v-html="place.equipment"></div>
              </div>
              <div class="place__description-border"></div>
              <div class="place__description-item">
                <h3 class="place__description-title">Назначение</h3>
                <div v-html="place.appointment"></div>
              </div>
            </div>
            <image-gallery
              v-if="place.images.length"
              :images="place.images"
            ></image-gallery>
          </div>
          <div class="place__col-right">
            <!--            <button class="button-primary place__btn-event">-->
            <!--              Провести мероприятие-->
            <!--            </button>-->
          </div>
        </div>
        <div class="places-list">
          <h2 class="places-list__title">Другие площадки</h2>
          <div class="place-cards">
            <place-card
              v-for="placeItem in place.random_places"
              :place="placeItem"
              :key="placeItem.id"
              :id="placeItem.id"
            >
            </place-card>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ImageGallery from "../components/ImageGallery";
import axios from "axios";
import mixin from "@/mixin/mixin";
import PlaceCard from "@/components/Cards/PlaceCard";

export default {
  name: "PlaceViewPage",
  components: { PlaceCard, ImageGallery },
  mixins: [mixin],
  data() {
    return {
      place: {},
      load: false
    };
  },
  async mounted() {
    await this.loadData();
  },

  methods: {
    async loadData() {
      let load = this.$loading.show();

      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/place/view`);
      url.searchParams.set("id", this.$route.params.id);
      url.searchParams.set("expand", "random_places");

      await axios
        .get(url)
        .then(response => {
          this.place = response.data;
          this.load = true;
          load.hide();
        })
        .catch(error => {
          if (error.response.status === 404) {
            this.$toasted.global.error("Площадка не найдена");
            this.$router.push("/places");
          } else {
            this.$toasted.global.error(error.response.data.message);
          }
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
            load.hide();
          }
          load.hide();
        });
    }
  }
};
</script>

<style scoped></style>
